export class BGCanvasRenderer {
    constructor(canvasManager){
        this.ctx = canvasManager.getContext();
    }

    renderBackground() {
        const cellSize = 10; // Adjust the size of each cell as needed
        const rows = this.ctx.canvas.height / cellSize;
        const cols = this.ctx.canvas.width / cellSize;

        // Iterate over rows and columns
        for (let i = 0; i < rows; i++) {
            for (let j = 0; j < cols; j++) {
                // Alternate between grey and white cells
                const color = (i + j) % 2 === 0 ? '#FFFFFF' : '#CCCCCC'; // Adjust colors as needed

                // Draw the cell
                this.ctx.fillStyle = color;
                this.ctx.fillRect(j * cellSize, i * cellSize, cellSize, cellSize);
            }
        }
    }
}
